import {Component, OnInit} from '@angular/core';
import {BibleFilterModalComponent} from '../common/bible-filter-modal/bible-filter-modal.component';
import {ModalController} from '@ionic/angular';
import {Location} from '@angular/common';
import {BibleService} from '../../services/bible.service';
import {EventProviderService} from "../../services/event-provider.service";

@Component({
    selector: 'app-bible-search',
    templateUrl: './bible-search.page.html',
    styleUrls: ['./bible-search.page.scss'],
})
export class BibleSearchPage implements OnInit {

    search_input: any = '';
    count: any;
    filtered_data: any;
    old_data: any;
    new_data: any;

    constructor(
        public modalController: ModalController,
        private location: Location,
        private _bibleService: BibleService,
        private event_provider: EventProviderService,
    ) {
    }

    async filterModal() {
        const modal = await this.modalController.create({
            component: BibleFilterModalComponent,
            cssClass: 'BibleModal',
            componentProps: {
                old_data: this.old_data,
                new_data: this.new_data,
                total_data: this.filtered_data
            }
        });
        modal.onDidDismiss().then(res => {

        });
        return await modal.present();
    }

    ngOnInit() {
        // event subscription
        this.event_provider.count.subscribe(value => {
            this.count = value.count;
            this.filtered_data = value.data;

            // Seperating Old testament data and New Testament Data ============================
            this.old_data = this.filtered_data.filter(x => {
                let check = x.dam_id.toString();
                if (check[6] === 'O') {
                    return x;
                }
            });
            this.new_data = this.filtered_data.filter(x => {
                let check = x.dam_id.toString();
                if (check[6] === 'N') {
                    return x;
                }
            });
            // ===============================================================================
        });

    }

    search() {
        if (this.search_input && this.search_input !== '') {
            this.event_provider.searchbible(  this.search_input );               // calling event
        }
    }

    gotobackPage() {
        this.location.back();
    }
}
