import {NgModule} from '@angular/core';
import {RouterModule, Routes} from '@angular/router';
import {BibleSearchPage} from './bible-search.page';

const routes: Routes = [
    {
        path: '',
        component: BibleSearchPage,
        children: [
            {
                path: 'teachings',
                children: [
                    {
                        path: '',
                        loadChildren: () => import('../teachings-search-list/teachings-search-list.module').then(m => m.TeachingsSearchListPageModule)
                    }
                ]
            },

            {
                path: 'bible',
                children: [
                    {
                        path: '',
                        loadChildren: () =>
                            import('../bible-search-list/bible-search-list.module').then(m => m.BibleSearchListPageModule)
                    }
                ]
            },
            {
                path: '',
                redirectTo: '/bible-search/bible',
                pathMatch: 'full'
            }
        ]
    },
    {
        path: '',
        redirectTo: '/bible-search/teachings',
        pathMatch: 'full'
    }
];

@NgModule({
    imports: [RouterModule.forChild(routes)],
    exports: [RouterModule]
})
export class BibleSearchRouterModule{
}
